.product {
    background: url(#{$imagesPath}h1-deal-bg_1296x.webp) no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
    margin-bottom: 100px;

    &__card {
        gap: 20px;
    }
}

.row.slick-slide {
    flex-direction: row;
}

.card__img-container_tab {
    flex-basis: 45%;
    margin-right: 20px;
    max-width: 100%;
}


.card__object_tab {
    margin-top: 0;
}

.sale__items .slick-track {
    gap: 20px;
}

