.trendy {
    &__title {
        margin-top: 50px;
    }
}

.card {
    column-gap: 20px;

    &__item {
        width: calc(25% - 15px);

        &:hover .watch {
            visibility: visible;
            opacity: 1;
            }
    
        &:hover .buy {
            visibility: visible;
            opacity: 1;
        }
    }
}

// hidden elements for cards

.watch {
    visibility: hidden;
    opacity: 0;
    transition: all 0.4s ease-in-out;
}

.buy {
    visibility: hidden;
    opacity: 0;
    transition: all 0.4s ease-in-out;
}