.inst {
    margin-top: 50px;

    &__title {
        margin: 0;
        font-size: 26px;
        text-align: center;
    }

    &__t {
        display: inline-block;
        position: relative;
        
        &::before,
        &::after {
            content: "";
            position: absolute;
            display: block;
            width: 45px;
            height: 1px;
            background-color: $black;
        }
    
        &::before {
            left: -65px;
            top: 50%;
        }
    
        &::after {
            right: -65px;
            top: 50%;
        }
    }

    &__items {
        margin-top: 20px;
        margin-bottom: 30px;
    }

    &__i {
        width: 100%;
        height: auto;
        cursor: pointer;
    }
}