.product {
    background: url(#{$imagesPath}h1-deal-bg_1296x.webp) no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
    margin-bottom: 100px;

    &__card {
        padding: 20px;
        background-color: $white;
        border-radius: 3px;
        box-shadow: 0 0 6px 0 rgba(1, 1, 1, 0.3);
        position: relative;
        top: 100px;
        display: flex;
        flex-wrap: wrap;
    }

    &__item {
        flex: 0 0 100%;
        max-width: 100%;
    }
    
    &__item-list {
        flex: 0 0 100%;
        max-width: 100%;
    }   
}

.row {
    display: flex;
    flex-direction: column;
}

.card__img-container_tab {
    max-width: 100%;
}

// rules for slider => card content will be same size
.c-slider .slick-track {
    display: flex;
}

.row.slick-slide {
    display: flex;
    height: auto;
}


// modificator for hidden elements
.like_s {
    top: 15px;
    right: 15px;
}

.watch_s {
    right: 5px;
}

.buy_s {
    right: 5px;
    bottom: 5px;
}

.card__link_s {
    font-size: 32px;
    line-height: 32px;
    font-weight: 700;
}

.card__price_s {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 21px;
}

.sale {
    // maybe, need to add a flexbox here -> title(of any size) will be on the same level
    &__count {
        display: flex;
        justify-content: space-between;
        margin-bottom: 12px;
    }

    &__text {
        margin: 0;
    }

    &__number {
        font-weight: 800;
    }

    &__item {
        width: calc(33% - 5px);
        opacity: 0.7;
        position: relative;
        cursor: pointer;

        &::before {
            border-top: 1px solid rgba(0,0,0,0.5);
            border-bottom: 1px solid rgba(0, 0, 0, 0.5);
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            opacity: 0;
            transform: scale(0,1);
            transition: opacity .45s, transform .45s;
        }

        &::after {
            border-right: 1px solid rgba(0, 0, 0, 0.5);
            border-left: 1px solid rgba(0, 0, 0, 0.5);
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            opacity: 0;
            transform: scale(1, 0);
            transition: opacity .45s, transform .45s;
        }
    }

    &__img {
        width: 100%;
    }
}

.loading {
    width: 100%;
    background: #efefef;
    margin-bottom: 30px;

    &__bar {
        display: block;
        background: #f76b6a;
        width: 42%;
        height: 12px;
        background-size: 40px 40px;
        background-image: linear-gradient(45deg, rgba(255, 255, 255, .15) 25%, rgba(0, 0, 0, 0) 25%, rgba(0, 0, 0, 0) 50%, rgba(255, 255, 255, .15) 50%, rgba(255, 255, 255, .15) 75%, rgba(0, 0, 0, 0) 75%, rgba(0, 0, 0, 0));
        animation: 2s linear 0s normal none infinite running progress-bar;
    }
}

@keyframes progress-bar {
    0% {
        background-position: 0 0;
    }
    100% {
        background-position: 40px 0;
    }
}

// when card active in sales__item -> border appear with animation
.slick-current.sale__item {
    opacity: 1;
}
.slick-current.sale__item::before {
    opacity: 1;
    transform: scale(1);
}

.slick-current.sale__item::after {
    opacity: 1;
    transform: scale(1);
}