.footer {

    &__header {
        display: flex;
        flex-wrap: wrap;
    }

    &__cont {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 30px;
    }

    &__form {
        flex-basis: 100%;
        display: flex;
        align-items: center;
    }

    &__title {
        margin: 0;
        margin-right: 30px;
    }

}

.logo_s {
    margin: 0;
}

.form {
    width: 100%;
}

.button_f {
    font-size: 14px;
    height: 39px;
    margin-top: 0;
    padding: 0 16px;
    letter-spacing: 1px;
}

.container-link {
    display: flex;
    flex-wrap: wrap;
    align-items: baseline;
}



.contacts {
    flex: 0 0 50%;   
}

.footer__links {
    flex: 0 0 50%;
}

.container-work {
    flex: 0 0 50%;
}