.collection {
    margin-top: 10px;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;

    &__link {
        text-decoration: none;
        color: $black;
        display: block;
        position: relative;
        width: 100%;
        font-size: 0;

        &_w {
            width: calc(50% - 5px);
        }
    }

    &__img {
        width: 100%;
    }

    &__content {
        position: absolute;
        top: 10%;
        left: 10%;
    }

    &__title {
        font-weight: 800;
        font-size: 18.125px;
        margin: 0;
        margin-bottom: 6px;
        line-height: 18px;
    }

    &__text {
        font-size: 25px;
        font-weight: 700;
        color: $grayText;
        margin: 0;
        line-height: 18px;
    }
}