.footer {

    &__container {
        padding: 40px 15px 0 15px;
        background-color: #fafafa;
        border-top: 1px solid #ececec;
        border-bottom: 1px solid #ececec;
    }

    &__header {
        margin-bottom: 30px;
    }

    &__items {
        list-style: none;
        padding: 0;
        display: flex;
        gap: 10px;
        margin: 15px 0;
    }

    &__link {
        display: block;
        width: 30px;
        height: 30px;
        background-color: #efefef;
        border-radius: 50%;
        font-size: 14px;
        text-decoration: none;
        color: #8f8f8f;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__title {
        font-weight: 800;
        font-size: 22px;
        margin-bottom: 10px;
    }

    &__ul {
        padding: 0;
        list-style: none;
        margin-top: 0;
        margin-bottom: 20px;
    }

    &__li {
        padding: 5px 0;
    }

    &__a {
        line-height: 2;
        font-size: 14.5px;
        text-decoration: none;
        color: inherit;
    }
}

.logo_s {
    margin: 0;
}

.form {
    display: flex;
    gap: 10px;
    position: relative;

    &__email {
        height: 39px;
        background-color: $white;
        width: 100%;
        padding: 0 10px;
        font-size: 14px;
        border: 1px solid $grayText;
    }
}

.warning {
    position: absolute;
    left: 0;
    bottom: -20px;
    color: red;
    font-size: 14px;
}

.button_f {
    font-size: 14px;
    height: 39px;
    margin-top: 0;
    padding: 0 16px;
    letter-spacing: 1px;
}

.contacts {
    margin-bottom: 20px;

    &__text {
        max-width: 220px;
        line-height: 2.5;
        font-size: 14.5px;
        margin: 0;
    }

    &__phone {
        color: inherit;
        line-height: 2.5;
        font-size: 14.5px;
        margin: 0;
        text-decoration: none;
        display: block;
    }

    &__mail {
        text-decoration: none;
        color: inherit;
        line-height: 2.5;
        font-size: 14.5px;
        margin: 0;
        display: block;
    }
}

.work {
    &__week {
        display: flex;
        justify-content: space-between;
        position: relative;

        &::after {
            content: "";
            display: block;
            position: absolute;
            width: 100%;
            height: 1px;
            background-color: #efefef;
            bottom: 0;
            left: 0;
        }
    }

    &__day {
        margin: 0;
        font-size: 14.5px;
        line-height: 2;
    }
}

.pay {
    text-align: right;
    margin: 20px 0;

    &__list {
        padding: 0;
        margin: 0;
        list-style: none;
    }

    &__item {
        display: inline-block;
    }
}

.copy {
    text-align: center;

    &__text {
        font-size: 14.5px;
    }
}
