.header {
    padding: 0 30px;
}

.burger-menu {
    display: none;
}

.menu {
    display: block;

    &__list {
        display: flex;
        list-style: none;
        padding: 0;
    }

    &__item {
        padding: 22px 15px;
        text-transform: uppercase;
        font-size: 14px;
        letter-spacing: 1.5px;
        font-weight: 700;
        cursor: pointer;
        position: relative;
    }
}

.cart-menu {
    right: -400px;
    width: 400px;
}

.cart-menu.visible {
    transform: translateX(-400px);
}

// hover menu DEMO
.hover-menu {
    display: flex;
    padding: 15px 30px;
    position: absolute;
    top: 80%;
    font-size: 1em;
    pointer-events: none;
    left: 0;
    z-index: 300;
    visibility: hidden;
    opacity: 0;
    border: 1px solid #f0f0f0;
    background-color: #fff;
    transition: opacity .2s ease .1s, visibility .2s ease .1s, top .2s ease .1s;

    &__column {
        width: 150px;
    }

    &__list {
        list-style: none;
        margin: 0;
        padding: 0;
    }

    &__list-sub {
        list-style: none;
        margin: 0;
        padding: 0;
    }

    &__link {
        margin-bottom: 2px;
        text-decoration: none;
        line-height: 28px;
        display: inline-block;
        text-transform: capitalize;
        position: relative;
        font-size: 14.5px;
        border-bottom: 2px solid transparent;
        color: $black;
        font-weight: 400;
        transition: all .3s ease-out;

        &_t {
            padding: 15px 0;
            font-size: 130%;
            font-weight: 700;
            display: block;
            color: $black;
            text-decoration: none;
        } 
    }

    &__container {
        max-width: 850px;
        width: 100%;
        padding-right: 10px;
        padding-left: 10px;
        margin-right: auto;
        margin-left: auto;
    }

    &__row {
        display: flex;
        flex-wrap: wrap;
        margin-right: -10px;
        margin-left: -10px;
    }
}

.menu__item:hover {
    color: #f76b6a;
}

.menu__item_demo:hover .hover-menu_demo {
    visibility: visible;
    opacity: 1;
    top: 100%;
    pointer-events: auto;
}

.hover-menu__link_t:hover {
    color: #f76b6a;
}

.hover-menu__list-sub .hover-menu__link:hover::before {
    content: "";
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #f76b6a;;
}



