// hover menu SHOP
.hover-menu_shop {
    display: block;
}

.product-name {
    flex: 0 0 75%;
    max-width: 75%;
    display: flex;
}

.product-item {
    flex: 0 0 25%;
    max-width: 25%;
    padding-top: 15px;
}

.menu__item_shop:hover .hover-menu_shop {
    visibility: visible;
    opacity: 1;
    top: 100%;
    pointer-events: auto;
}

.hover-menu_shop .hide__link {
