// hover for hide elements in card
.like__link::before, .hide__link::before {
    content: '';
    position: absolute;
    right: 100%;
    bottom: 50%;
    margin-right: 2px;
    margin-bottom: -6px;
    background: 0 0;
    border-top: 6px solid transparent;
    border-left: 12px solid #383838;;
    border-bottom: 6px solid transparent;
    transform: translate3d(0, 0, 0);
    visibility: hidden;
    pointer-events: none;
    opacity: 0;  
}

.hide__link::before {
    margin-right: 12px;
}

.like__link:hover::before, .hide__link:hover::before {
    visibility: visible;
    opacity: 1;
}

.like__link::after, .hide__link::after {
    content: "Wishlist";
    position: absolute;
    background: #383838;
    color: #fff;
    right: 100%;
    bottom: 50%;
    margin-right: 8px;
    margin-bottom: -14px;
    padding: 8px 10px;
    font-size: 12px;
    line-height: 12px;
    white-space: nowrap;
    transform: translate3d(0, 0, 0);
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
    z-index: 999999;
}

.like__link:hover::after, .hide__link:hover::after {
    visibility: visible;
    opacity: 1;
}

.hide__link::after {
    margin-right: 18px;
}

.hide__link_b::after {
    content: "Quick shop";
}

.hide__link_w::after {
    content: "Quick view";
}