.collection {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap : 10px;

    &__link {

        &_w {
            grid-row: 1;
            grid-column: 4;
            width: 100%;

            &:nth-child(2) {
                grid-row: 1;
                grid-column: 3;
                width: 100%;
            }
        }

        &_1 {
            grid-row: 1 / span 2;
            grid-column: 1 / span 2;
        }

        &_2 {
            grid-row: 2;
            grid-column: 3 / span 2;
        }
    }

    &__title {
        font-size: 29px;
        line-height: 29px;
    }

    &__text {
        font-size: 25px;
        line-height: 28px;
    }
}

