.product {
    background: url(#{$imagesPath}h1-deal-bg_1296x.webp) no-repeat;
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
    margin-bottom: 100px;

    &__card {
        flex-wrap: nowrap;
        gap: unset;
    }

    &__item {
        flex: 0 0 75%;
        max-width: 75%;

        &:hover .watch {
            visibility: visible;
            opacity: 1;
        }
        
        &:hover .buy {
            visibility: visible;
            opacity: 1;
        }
    }

    &__item-list {
        flex: 0 0 25%;
        max-width: 25%
    }
}

.c-slider .card__content {
    flex: 0 0 55%;
}

.watch {
    visibility: hidden;
    opacity: 0;
    transition: all 0.4s ease-in-out;
}

.buy {
    visibility: hidden;
    opacity: 0;
    transition: all 0.4s ease-in-out;
}



.sale {
    // maybe, need to add a flexbox here -> title(of any size) will be on the same level

    &__items {
        padding-left: 115px;
    }

    &__item {
        margin:10px 0;
    }
}

.sale__items .slick-track {
   display: block;
}

