.blog {
    padding-top: 30px;

    &__title {
        font-size: 32px;
        font-weight: 800;
        text-align: center;
        margin: 0;
        margin-bottom: 10px;
    }

    &__text {
        opacity: 0.9;
        font-style: italic;
        line-height: 1.5;
        color: #878787;
        margin: 0;
        margin-bottom: 20px;
        text-align: center;
    }

    &__img {
        width: 100%;
        cursor: pointer;
        overflow: hidden;
    }

    &__i {
        width: 100%;
        transition: transform 0.8s ease-in-out;
    
    }

    &__item-slider {
        position: relative;
    }

    &__item {
        border: 1px solid rgba(0, 0, 0, 0.12);
        border-radius: 3px;
        margin: 0 5px;
        
        &:hover .blog__i  {
            transform: scale(1.2);
        }
    }

    &__action {
        display: flex;
        align-items: center;
    }

    &__link {
        color: $blackText;
        text-decoration: none;
        font-size: 13px;

        &:nth-of-type(2)::before {
            content: "";
            display: inline-block;
            width: 1px;
            height: 10px;
            background-color: $blackText;
            margin-right: 10px;
            margin-left: 10px;
        }

        &:hover {
            color: #f76b6a;
        }
    }
}

.blog-item {
    padding: 25px;
    display: flex;
    flex-direction: column;
    flex: 1;


    &__sign {
        text-decoration: none;
        text-transform: uppercase;
        margin: 0;
        font-size: 13px;
        letter-spacing: 2px;
        color: $blackTitle;
        opacity: 0.4;
        font-weight: 600;

        &:hover {
            color: $black;
            opacity: 0.8;
        }
    }

    &__title {
        text-decoration: none;
        font-weight: 700;
        font-size: 22px;
        margin: 10px 0 10px 0;
        color: $black;

        &:hover {
            color: #f76b6a;
        }
    }

    &__text {
        margin: 0;
        margin-top: auto;
        line-height: 1.75;
        font-size: 14px;
        margin-bottom: 10px;
    }
}

.blog-item__text-cont {
    display: flex;
    flex-direction: column;
    flex: 1;
}

.fa-message {
    margin-right: 5px;
    font-size: 16px;
}

.fa-bookmark {
    margin-right: 5px;
    font-size: 16px;
}

// rules for slick slider -> content of card will be same size.
.blog__item-slider .slick-track {
    display: flex;
}

.blog__item.slick-slide {
    display: flex;
    height: auto;
    flex-direction: column;
}