.header-slider {
    height: 300px;
    position: relative;

    &_1 {
        background: url(#{$imagesPath}slider01_1296x.webp) no-repeat;
        background-size: cover;
        background-position: top;
    }

    &_2 {
        background: url(#{$imagesPath}slider02_1296x.webp) no-repeat;
        background-size: cover;
        background-position: top;
    }

    &_3 {
        background: url(#{$imagesPath}slider03_1296x.webp) no-repeat;
        background-size: cover;
        background-position: top;
    }

    &__content {
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        padding: 0 10px 0 10px;
    }

    &__text {
        font-size: 7px;
        letter-spacing: 6px;
        text-transform: uppercase;
        font-weight: 600;
        margin-top: 0;
        color: #666;
        margin-bottom: 11px;
    }

    &__title {
        font-size: 17px;
        font-weight: 800;
        margin: 0;
        line-height: normal;
    }
}

.button {
    margin-top: 13px;
    padding: 6px 10px;
    background-color: $redBut;
    color: $white;
    text-transform: uppercase;
    font-size: 10px;
    letter-spacing: 2px;
    font-weight: 600;
    font-family: Arial;
    cursor: pointer;

    &:hover {
        opacity: .8;
    }
}

.header-slider__content .button:hover {
    opacity: .8;
}

// =============
//slick theme (do refactor code) this is attemp that slider is working
.header-slider__content > * {
    visibility: hidden;
    opacity: 0;
    transition: transform .3s, opacity .5s ease-in;
    transform: translate3d(70px, 0, 0);
}

.slick-current .header-slider__content > * {
    transform: translate3d(0, 0, 0) scale(1);
    visibility: visible;
    opacity: 1;
}


.slick-current .header-slider__content > .header-slider__title {
    transition: transform .7s, opacity .5s ease-in;
}

.slick-current .header-slider__content > .button {
    transition: transform 1.3s, opacity .5s ease-in;
}

