//fonts
$google-font: "https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400&display=swap";
$prefix: prefix- !default;
$baseFontSize: 16px;
$baseFontFamily: 'Nunito Sans', sans-serif;
$mediumFontWeight: 400;

$mobilePadding: (12px / $baseFontSize) * 1rem;
$desktopPadding: 1rem;

// Paths

$imagesPath: "../i/";
$fontsPath: "../fonts/";
$usersFilesPath: "../userfiles/";

// Colors
$black: #000000;
$white: #ffffff;

$redBut: #f76b6a;
$purpleIcon: rgb(120, 40, 230);
$blackTitle: #333333;
$blackText: #666;
$grayText: #999;
$linkCat: #ccc;


$colors: (
  "black": $black,
  "white": $white
) !default;
