.header-slider {
    background: url(#{$imagesPath}slider02_1296x.webp) no-repeat;
    background-size: cover;
    background-position: top;
    height: 408px;

    &_1 {
        background: url(#{$imagesPath}slider01_1296x.webp) no-repeat;
        background-size: cover;
        background-position: top;
    }

    &_2 {
        background: url(#{$imagesPath}slider02_1296x.webp) no-repeat;
        background-size: cover;
        background-position: top;
    }

    &_3 {
        background: url(#{$imagesPath}slider03_1296x.webp) no-repeat;
        background-size: cover;
        background-position: top;
    }

    &__content {
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        padding: 0 10px 0 10px;
    }

    &__text {
        font-size: 16px;
        line-height: 17px;
        letter-spacing: 12px;
        font-weight: 800;
        margin-top: 0;
        margin-bottom: 30px;
    }

    &__title {
        line-height: 1;
        font-size: 55px;
    }
}

.button {
    margin-top: 38px;
    padding: 12px 40px;
    font-size: 12px;
}