.trendy {

    &__title {
        margin-top: 30px;
        margin-bottom: 0;
        text-align: center;
        font-size: 32px;
        font-weight: 800;
    }

    &__categories {
        margin-top: 20px;
        margin-bottom: 30px;
    }

    &__list {
        list-style: none;
        padding: 0;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 10px;
    }

    &__link {
        color: $linkCat;
        font-weight: 600;
        font-size: 15px;
        text-transform: uppercase;
        letter-spacing: 2px;
        padding: 0 20px;
        position: relative;
        font-family: inherit;
        cursor: pointer;

        &:hover {
            color: $blackTitle;
        }

        &:hover::after {
            content: "";
            display: block;
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            bottom: 0;
            width: calc(100% - 40px);
            height: 2px;
            background-color: $blackTitle;
        }
    }
}

.trendy__link.active {
    color: $blackTitle;

    &::after {
        content: "";
        display: block;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 0;
        width: calc(100% - 40px);
        height: 2px;
        background-color: $blackTitle;
    }
}

.card {
    display: flex;
    flex-wrap: wrap;
    column-gap: 10px;

    &__item {
        display: flex;
        flex-direction: column;
        width: calc(50% - 5px);
        margin-bottom: 50px;
    }

    &__img-container {
        position: relative;
    }

    &__img {
        width: 100%;
    }

    &__content {
        margin-top: 20px;
        display: flex;
        flex-direction: column;
        flex: 1;
    }

    &__link {
        color: $blackTitle;
        text-decoration: none;

        &_img {
            display: block;
            font-size: 0;
        }

        &_name {
            font-size: 16px;

            &:hover {
                color: $redBut;
            }
        }
    }

    &__object {
        margin-top: auto;
        display: flex;
        flex-direction: column;
    }

    &__price {
        margin: 0;
        margin-top: 10px;
        font-size: 18px;
        font-weight: 600;
    }

}

.color {
    display: flex;
    column-gap: 10px;
    align-items: center;
    margin-top: 6px;
    
    &__item {
        width: 16px;
        height: 16px;
        border-radius: 50%;
        display: block;

        &_light {
            background-color: #daeceb;
        }

        &_gray {
            background-color: #bfbfbf;
        }

        &_yellow {
            background-color: #dcdb79;
        }
    }
}


.like {
    position: absolute;
    top: 15px;
    right: 15px;
    font-size: 19px;
}

.watch {
    width: 40px;
    height: 40px;
    background-color: #383838;
    position: absolute;
    font-size: 18px;
    border-radius: 50%;
    right: 5px;
    top: 65px;
}

.buy {
    position: absolute;
    width: 40px;
    height: 40px;
    background-color: $redBut;
    border-radius: 50%;
    bottom: -18px;
    right: 5px;
}

.hide__link {
    display: inline-block;
    color: $white;
    font-size: 18px;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

// change heart color from fontAwasome

.fa-heart {
    color: $black;
}

// sale 
.sale .card__img-container::before {
    content: "-38%";
    display: block;
    position: absolute;
    background-color: #95bf47;
    color: $white;
    letter-spacing: .4px;
    font-size: 12px;
    padding: 3px 10px;
    left: 0;
    top: 20px;
}

