.header-slider {
    &_1 {
        background: url(#{$imagesPath}slider01_1296x.webp) no-repeat;
        background-size: cover;
        background-position: top;
    }

    &_2 {
        background: url(#{$imagesPath}slider02_1296x.webp) no-repeat;
        background-size: cover;
        background-position: top;
    }

    &_3 {
        background: url(#{$imagesPath}slider03_1296x.webp) no-repeat;
        background-size: cover;
        background-position: top;
    }

    &__content {
        padding: 0 30px 0 30px;
    }
}

.h-slider .slick-prev {
    left: 20px;
    opacity: 0;
    z-index: 9;
    height: 55px;
    

    &::before {
        font-family: "Font Awesome 6 Free";
        display: block;
        content: "\f053";
        font-weight: 600;
        width: 55px;
        font-size: 40px;
        opacity: .7;
        height: 55px;
        line-height: 55px;
        color: $black;
    }
}

.h-slider:hover .slick-prev {
    left: 0;
    opacity: 1;
}

.h-slider .slick-next {
    right: 20px;
    opacity: 0;
    z-index: 9;
    height: 55px;


    &::before {
        font-family: "Font Awesome 6 Free";
        display: block;
        content: "\f054";
        font-weight: 600;
        width: 55px;
        font-size: 40px;
        opacity: .7;
        height: 55px;
        line-height: 55px;
        color: $black;
    }
}

.h-slider:hover .slick-next {
    right: 0;
    opacity: 1;
}


